
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'


import MyAudio from './Common/my-audio'

import {
    cloneDeep,
    find,
    forEach
} from 'lodash/'

export default {
    components: {
        XHeader,
        Group,
        Box,
        Icon,
        Popup,
        Flexbox,
        MyAudio
    },
    data() {
        return {
            topic: {},
            play: {
                title: '',
                id: '',
                show: false,
                status: '',
                list: []
            }
        }
    },
    created() {
        if (this.$store.getters.topicList.length == 0) {
            this.$router.push('/Auth')
            return
        }

        this.topic = cloneDeep(find(this.$store.getters.topicList, {
            id: this.$route.query.id
        }))
    },
    computed: {},
    methods: {
        playMedia(id) {
            let t = find(this.topic.docList, {
                id
            })

            this.play.title = t.title
            this.play.id = t.id

            // 加入播放列表
            let doc = find(this.play.list, {
                id: t.id
            })
            if (!doc) {
                doc = {
                    id: t.id,
                    src: t.src,
                    status: 'play'
                }
                this.play.list.push(doc)
            } else {
                doc.status = 'play'
            }

            this.play.show = true
            doc.status = 'play'
        },
        pauseMedia() {
            forEach(this.play.list, (val) => {
                val.status = 'pause'
            })
        }
    }
}



